import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { FormControl, TextField, InputAdornment } from '@material-ui/core';
import { Search } from '@material-ui/icons';
import { debounce } from 'lodash';

import SearchResults from './SearchResults';
import { useSelector } from 'react-redux';

export interface Props {}

const useStyles = makeStyles((theme) => ({
  form: {
    margin: theme.spacing(1),
  },
}));

export const SearchInput: React.FC<Props> = (props) => {
  const classes = useStyles();

  const [search, setSearch] = useState<string>('');
  const [suggestions, showSuggestions] = useState<boolean>(false);
  const [searchedResult, setSearchedResult] = useState<string[]>([]);

  const totalCoins = useSelector((state: any) => state.search.totalCoins);

  const onInputBlur = () => {
    showSuggestions(false);
  };

  const onInputFocus = () => {
    showSuggestions(true);
  };

  const searchHandler = (searchText: any = '') => {
    if (totalCoins.length > 0) {
      const filteredSearches = totalCoins.filter((coin: string) => {
        if (coin.toLowerCase().includes(searchText.toLowerCase())) {
          return coin;
        }
      });
      setSearchedResult(filteredSearches);
    }
  };

  const InputProps = {
    startAdornment: (
      <InputAdornment position="start">
        <Search fontSize="large" />
      </InputAdornment>
    ),
    disableUnderline: true,
    style: { fontSize: 14, color: '#323232' },
    onBlur: onInputBlur,
    onFocus: onInputFocus,
    value: search,
  };

  return (
    <FormControl className={classes.form}>
      <TextField
        id="coin-search-input"
        placeholder="Search coins"
        value={search}
        InputProps={InputProps}
      />
      {suggestions && <SearchResults />}
    </FormControl>
  );
};

export default SearchInput;
